%footer-layout {
  background-color: $tingegrey;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;

  @media screen and (min-width: 1088px) {
      flex-direction: row;
      justify-content: space-between;
  }

  .links {
    font-size: 0.875rem;
    margin-top: 1rem;
    margin-bottom: 0;
    display: inline-block;
    justify-content: space-between;
    text-align: center;
    @media screen and (min-width: 1088px) {
        margin-top: 0;
    }
    & > li {
      display: inline-block;
      margin: 0.25rem 1.5rem 0 0;
      a {
        color: $body-color;
        &:active, &a:hover {
          color: $darkgrey;
          text-decoration: underline;
        }
      }
    }
  }
}

.footerContainer {
    width: 100%;
    //margin: 1.0rem 0 0;
    border-top: 1px solid $white;

    .footer-layout-home {
      @extend %footer-layout;
    }
    
    .footer-layout {
      border-top: 10px solid $deep-red;
      @extend %footer-layout;
    }

    .locale-selector-link {
      color: $body-color;
      font-size: 0.875rem;
      display: flex;
      align-items: center;
      &:hover {
        color: $darkgrey;
        text-decoration: none;
        .local-selector-link-text {
          text-decoration: underline;
        }
      }
    }

    .site-resources {
      padding: 0;
      background-color: $link-hover-deep-red;
      .site-resources-inner {
        margin-top: 0;
        position: relative;
        h2 {
          margin: 0;
          padding: 16px 0 0;
          font-size: 1rem;
          color: $white;
          @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
              font-weight: 600;
          }
          @media screen and (min-width: 768px), print {
              font-size: 1.5rem;
          }
        }
        ul {
          display: flex;
          flex-direction: column;
          margin: 0;
          padding: 0 0 24px;
          @media screen and (min-width: 768px), print {
              flex-direction: row;
              width: 100%;
          }
          li {
            list-style-type: none;
            margin: 1rem 0 0;
            margin-right: 3rem;
            a {
              font-size: 1rem;
              color: $white;
              &.focus-visible {
                outline-color: $white;
              }
            }
          }
        }
      }
    }

    .footer-bar-logo {
      position: relative;
       width: 144px;
       height: 40px;
       background-size: contain;
  
      .overlay {
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        opacity: 0;
        transition: .4s ease;
        background-color: $white;
      }
  
      &:hover .overlay {
        opacity: 1;
      }
  
      .alt-logo {
        color: $red;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        text-align: center;
      }
    }

  }








