/* LEFT TOC STYLING */

/* Filter Bar */
.visually-hidden {
  position: absolute;
  overflow: hidden; 
  height: 1px;
  width: 1px; 
  padding: 0;
  margin: -1px;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  word-wrap: normal;
}

/* Navigation Menu */

.toc{
  margin: 0px;
}

ul.toc {
  position: relative;
  font-size: .825rem;
  ul {
    position: relative;
    font-size: .825rem;
  }
  & > ul{
    padding: 0px 0px;
    text-decoration: none;
    & .treegroup:not(.is-expanded){
      display: none;
    }
  }
  & > ul ul {
    padding: 0px 0px;
    margin-left: 1.5rem;
    text-decoration: none;
  }
  a {
    display: block;
    padding: 0px 0px;
    margin: 0px 0px;
    color: $body-color;
    text-decoration: none;
  }
  li{
    padding: 2px 1rem;
    color: $body-color;
    text-decoration: none;
    cursor: pointer;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    &.is-active, &.is-active a {
      font-weight: 500;
      background-color: $pearlblue;
      color: $cleanblue;
    }
    &:hover:not(.is-active){
      background-color: $mistygrey;
    }
    &:hover:not(.is-active) a {
      background-color: $mistygrey;
      text-decoration: underline;
    }
  }

  /* Chevron */
  .chevron{
    height:0.5rem;
    width: 0.5rem;
  }

  .tree-expander{
    display: block;
    position: relative;
    padding-left: 0px;
    padding-top: 0px;
    padding-bottom: 0px;
    cursor: pointer;
    user-select: none;
  }

  .tree-indicator{
    display: inline-block;
    position: absolute;
    top: 6px;
    left: -1.5em;
    line-height: 16px;
    font-size: 0.6rem;
    text-align: center;
    color: $darkgrey;
    transition: transform 0.15s ease-in-out;
    transform: rotate(0deg);
  }

  .treeitem.is-expanded > .tree-expander > .tree-indicator {
    transform: rotate(90deg);
  }

}

%button{
  display: flex;
  align-items: center;
  max-width: 3.4em;
  padding: 0.70rem;
  background-color: $sharp-red;
  color: $white;
  box-shadow:0 3px 12px 0 $shadow-color;
  transition-property: all;
  transition-duration: 120ms;
  transition-timing-function: linear;
  transition-delay: 0s;
}

%buttonHover{
  background-color: $white; 
  color: $deep-red;
  border: 1px solid $deep-red;
  -webkit-box-shadow:0 4px 14px 0 $shadow-color;
  box-shadow:0 4px 14px 0 $shadow-color;
  border-radius: 2px; 
}

%isFaded{
  background-color: $tingegrey;
  color: $grey;
}

%isFadedHover{
  background-color: $tingegrey;
  color: $grey;
  border: 1px solid $darkish-grey;
  -webkit-box-shadow:0 4px 14px 0 $shadow-color;
  box-shadow:0 4px 14px 0 $shadow-color;
}

#earlierPage{
  @extend %button;
  margin-left: 0.2rem;
  &:hover{
      @extend %buttonHover; 
  }
  &.is-faded{
  @extend %isFaded; 
      &:hover{
          @extend %isFadedHover; 
      }
  }

}


