/* ELEMENTS */
input {
  line-height: normal;
  margin: 0;
  overflow: visible;
  font-family: inherit;
  font-size: 100%;
}

h1, h2.likeAnH1, h2, h3, h4, h5, h6 {
  font-weight: 500;
}

.tabs:not(:last-child), .box:not(:last-child), .progress:not(:last-child), .title:not(:last-child),
.subtitle:not(:last-child), .level:not(:last-child) {
  margin-bottom: 1.5rem;
}

.button, .checkbox-button, .file-cta, .file-name, .input, .pagination-ellipsis, .pagination-link, .pagination-next, .pagination-previous, .select select, .textarea {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  vertical-align: top;
  -webkit-appearance: none;
  -moz-appearance: none;
  position: relative;
  height: 2.25em;
  padding-bottom: calc(.375em - 1px);
  padding-left: calc(.625em - 1px);
  padding-right: calc(.625em - 1px);
  padding-top: calc(.375em - 1px);
  border: 1px solid $tingegrey;
      border-top-color: $tingegrey;
      border-right-color: $tingegrey;
      border-bottom-color: $tingegrey;
      border-left-color: $tingegrey;
  border-radius: 4px;
  box-shadow: none;
  font-size: 1rem;
  line-height: 1.5;
  appearance: none;
}

.pagination-previous,
.pagination-next,
.pagination-link,
.pagination-ellipsis, .button, .input,
.textarea, .select select, .file-cta,
.file-name, .checkbox-button {
appearance: none;
align-items: center;
border: 1px solid transparent;
border-radius: 4px;
box-shadow: none;
display: inline-flex;
font-size: 1rem;
height: 2.25em;
justify-content: flex-start;
line-height: 1.5;
padding-bottom: calc(0.375em - 1px);
padding-left: calc(0.625em - 1px);
padding-right: calc(0.625em - 1px);
padding-top: calc(0.375em - 1px);
position: relative;
vertical-align: top;
}

.delete, .modal-close-button, .modal-card-close-button, .pagination-previous,
.pagination-next,
.pagination-link,
.pagination-ellipsis, .tabs, .nav-bar-button, .button, .file {
  /* stylelint-disable */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  /* stylelint-enable */
  user-select: none;
}

.delete, .modal-close-button, .modal-card-close-button {
  color: inherit;
  /* stylelint-disable */
  -moz-appearance: none;
  -webkit-appearance: none;
  /* stylelint-enable */
  background-color: transparent;
  border: none;
  border-radius: 290486px;
  cursor: pointer;
  display: inline-block;
  flex-grow: 0;
  flex-shrink: 0;
  font-size: 0;
  height: 20px;
  max-height: 20px;
  max-width: 20px;
  min-height: 20px;
  min-width: 20px;
  outline: none;
  position: relative;
  vertical-align: top;
  width: 20px;
}

.delete:before, .modal-close-button:before, .modal-card-close-button:before, .delete:after, .modal-close-button:after, .modal-card-close-button:after {
  background-color: currentColor;
  content: '';
  display: block;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
  transform-origin: center center;
  border: 1px solid transparent;
}

.delete:before, .modal-close-button:before, .modal-card-close-button:before {
  width: 50%;
}

.delete:after, .modal-close-button:after, .modal-card-close-button:after {
  height: 50%;
}

.delete:hover:before, .modal-close-button:hover:before, .modal-card-close-button:hover:before, .delete:hover:after, .modal-close-button:hover:after, .modal-card-close-button:hover:after, .focus-visible.delete:before, .focus-visible.modal-close-button:before, .focus-visible.modal-card-close-button:before, .focus-visible.delete:after, .focus-visible.modal-close-button:after, .focus-visible.modal-card-close-button:after {
  border-color: currentColor;
}

.delete:active:before, .modal-close-button:active:before, .modal-card-close-button:active:before, .delete:active:after, .modal-close-button:active:after, .modal-card-close-button:active:after {
  border-color: $darkgrey;
}

button {
  color: $body-color;
  background-color: transparent;
  cursor: pointer;
}

.button{
  border-radius: 2px;
  background-color: $white;
  border-color: $darkgrey;
  border-width: 1px;
  color: $body-color;
  cursor: pointer;
  justify-content: center;
  padding-bottom: calc(0.375em - 1px);
  padding-left: 0.75em;
  padding-right: 0.75em;
  padding-top: calc(0.375em - 1px);
  text-align: center;
  white-space: nowrap;

  &:not(.is-text), .button:not(.is-text-primary) {
    text-decoration: none !important;
  }

  &.is-extra-small {
    border-radius: 2px;
    font-size: 0.75rem;
    padding-top: calc(0.375em - 1px);
    padding-bottom: calc(0.375em - 1px);
    padding-right: 1em;
    padding-left: 1em;
  }
  &.is-extra-small .icon {
    height: 0.75rem;
  }
  &.is-small {
    border-radius: 2px;
    font-size: 0.875rem;
  }
  &.is-small .icon {
    height: 1.1rem;
  }
  @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
    &.is-small-mobile {
      border-radius: 2px;
      font-size: 0.875rem;
    }
    &.is-small-mobile .icon {
      height: 0.875rem;
    }
  }
  &.is-medium {
    font-size: 1rem;
  }
  &.is-medium .icon {
    height: 1rem;
  }
  &.is-large {
    font-size: 1.125rem;
  }
  &.is-large .icon {
    height: 1.125rem;
  }
  @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
    &.is-large-mobile {
      font-size: 1.125rem;
    }
    &.is-large-mobile .icon {
      height: 1.125rem;
    }
  }

  &.is-primary {
    background-color: $dax-red;
    border-color: $dax-red;
    color: $white;
  }
  &.is-primary.dax {
    background-color: $dax-red;
    border-color: $dax-red;
    color: $white;
  }
  &.is-primary.articles-and-blogs {
    background-color: $light-normal-red;
    border-color: $light-normal-red;
    color: $white;
  }

  &.is-primary:hover, &.is-primary.is-hovered {
    background-color: $dax-color;
    border-color: $dax-red;
    color: $white;
  }
  &.is-primary.dax:hover, &.is-primary.dax.is-hovered {
    background-color: $dax-color;
    border-color: $dax-red;
    color: $white;
  }
  &.is-primary.articles-and-blogs:hover, &.is-primary.articles-and-blogs.is-hovered {
    background-color: $articles-and-blogs-color;
    border-color: $normal-red;
    color: $white;
  }

  &.is-primary:visited {
    color: $white;
  }
  &.is-primary.focus-visible, &.is-primary.is-focused {
    border-color: $white;
    color: $white;
  }
  &.is-primary.dax:active, &.is-primary.dax.is-active {
    background-color: $dark-dax-red;
    border-color: $dark-dax-red;
    color: $white;
  }
  &.is-primary.articles-and-blogs:active, &.is-primary.articles-and-blogs.is-active {
    background-color: $dark-red;
    border-color: $dark-red;
    color: $white;
  }

  @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
    &.is-icon-only-mobile .icon {
      margin: 0 !important;
    }
  }

  .icon, .icon.is-small, .icon.is-medium, .icon.is-large {
    width: 1.5em;
  }
  
  .icon:first-child:last-child {
    margin-left: calc(-0.375em - 1px);
    margin-right: calc(-0.375em - 1px);
  }
  
  .icon:first-child:not(:last-child) {
    margin-left: calc(-0.375em - 1px);
    margin-right: 0.1875em;
  }
  
  .icon:last-child:not(:first-child) {
    margin-left: 0.1875em;
    margin-right: calc(-0.375em - 1px);
  }
}

.link-button {
  border: none;
  cursor: pointer;
  color: $link-default;
  display: inline-block;
  padding: 0;

  &:active, &.is-active {
    text-decoration: underline !important;
  }

  &.focus-visible, &.is-focused, &:hover, &.is-hovered {
    color: $link-hover;
    text-decoration: underline !important;
  }

  &:visited {
    color: $link-default;
  }
}


.card {
  outline-color: $body-color;
  color: $body-color;
  max-width: 100%;
  position: relative;
  padding: 1.5rem 0 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 0.125rem;

  -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 1s; /* Firefox < 16 */
  -ms-animation: fadein 1s; /* Internet Explorer */
  -o-animation: fadein 1s; /* Opera < 12.1 */
  animation: fadein 1s;

  @keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
  }

  /* Firefox < 16 */
  @-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
  }

  /* Safari, Chrome and Opera > 12.1 */
  @-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
  }

  /* Internet Explorer */
  @-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
  }

  /* Opera < 12.1 */
  @-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
  }


  &.dax{
    border: 1px solid $white;
    box-shadow: 0 1.6px 3.6px 0 $lightshadow-color, 0 0.3px 0.9px 0 $lightshadow-color;
    background-color: $white;
  }

  &.articles-and-blogs{
    border: 1px solid transparent;
    background-color: transparent;
  }

  & > :first-child {
    border-top-right-radius: 0.125rem;
    border-top-left-radius: 0.125rem;
  }
  & > :last-child {
    border-bottom-right-radius: 0.125rem;
    border-bottom-left-radius: 0.125rem;
  }
  &.is-uniform {
    padding: 1.5rem !important;
  }

  .card-header,
  .card-content,
  .card-footer {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  }

  .card-header:first-child,
  .card-content:first-child,
  .card-footer:first-child {
  padding-top: 1.5rem;
  margin-top: -1.5rem;
  }

  .card-content {
    flex-grow: 1;
    margin-bottom: 1.125rem;
  }

  .card-content > *:not(:first-child) {
    margin-top: 0.5rem;
  }

  .card-content .card-content-super-title {
    letter-spacing: 0.225rem;
    line-height: 1;
    text-transform: uppercase;
    font-size: 0.75rem;
  }

  .card-content .card-content-title {
    flex-grow: 1;
    font-weight: 600;
    line-height: 1.25;
    text-align: left;
    -webkit-line-clamp: 3;
    &:hover{
      text-decoration: underline;
    }

    &.dax{
      font-size: 1rem;
    }
  
    &.articles-and-blogs{
      font-size: 1.5rem;
      font-family: 'Raleway', sans-serif;
      & h3 {font-weight: 700 !important;}
    }
  }

  .card-content .card-content-title:first-child {
    margin-top: none;
  }

  .card-content .card-content-title:only-child {
    margin: 0;
  }

  .card-content button.card-content-title {
    appearance: none;
    border: none;
    padding: 0;
    margin: 0;

    &.dax{
    color: $normal-red;
    }

    &.articles-and-blogs{
      color: $black;
    }
  }

  .card-content button.card-content-title:hover {
    text-decoration: underline;
    cursor: pointer;
  }

  .card-content button.card-content-title:visited {
    &.dax{
      color: $normal-red;
      }
    
    &.articles-and-blogs{
      color: $black;
      }
  }

  .card-content a.card-content-title {
    &.dax{
      color: $normal-red;
      }
    &.articles-and-blogs{
      color: $black;
      }
  }

  .card-content a.card-content-title:visited {
    &.dax{
      color: $normal-red;
      }
    &.articles-and-blogs{
      color: $black;
      }
  }

  .card-content a.card-content-title:hover {
    &.dax{
      color: $normal-red;
      }
    &.articles-and-blogs{
      color: $mickblue;
      }
  }

  .card-content ul.card-content-metadata {
    color: $dategrey;
    line-height: 0.875rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    list-style: none;
    margin-bottom: 0;
    margin-right: 0;
    margin-left: 0;
    padding: 0;

    &.dax{
      font-size: 0.75rem;
    }

    &.articles-and-blogs{
      font-size: 0.85rem;
    }
  }

  .card-content ul.card-content-metadata .star-rating-display .star,
  .card-content ul.card-content-metadata .star-rating-display .before-text,
  .card-content ul.card-content-metadata .star-rating-display .explanation-text {
  font-weight: normal;
  line-height: 0.875rem;

  &.dax{
    font-size: 0.75rem;
  }

  &.articles-and-blogs{
    font-size: 0.85rem;
  }
  }

  .card-content ul.card-content-metadata .star-rating-display .star {
    color: $darkgrey;
  }

  .card-content ul.card-content-metadata > li {
    list-style: none;
    display: flex;
    flex-basis: auto;
    margin-bottom: 0.25rem;
  }

  .card-content ul.card-content-metadata > li:not(:only-child):not(:last-child) {
    margin-right: 0.75rem;
  }

  .card-content ul.card-content-metadata > li:not(:only-child):not(:first-child) {
    //padding-right: 0.75rem;
  }

  .card-content ul.card-content-metadata > li:first-of-type, .card-content ul.card-content-metadata > li:only-of-type {
    margin-left: 0;
  }

  @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
    .card-content ul.card-content-metadata > li:last-of-type {
      margin-left: 0;
    }
  }

  .card-content ul.card-content-metadata.has-bullets .docon {
    font-size: 13px;
    line-height: inherit;
    margin-right: 5px;
    width: 14px;
  }

  .card-content ul.card-content-metadata.has-bullets > li:not(:last-of-type):not(:only-of-type):after {
    padding-left: 5px;
    padding-right: 5px;
    content: '';
  }

  .card-content p.card-content-description {
    line-height: 1.25;
    font-size: 0.875rem;

    &.articles-and-blogs{
      font-family: 'Raleway', sans-serif;
      font-weight: 450;
      line-height: 1.6;
      font-size: 1.0rem;
    }
  }

  @supports (display: grid) {
   .card-content p.card-content-description {
      -webkit-line-clamp: 4;
      max-height: inherit !important;
      padding-bottom: 0 !important;

      &.articles-and-blogs{
        font-family: 'Raleway', sans-serif;
        font-weight: 450;
      }

    }
   .card-content p.card-content-description:after {
      display: none;
    }
  }

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .card-content p.card-content-description {
      position: relative;
      overflow: hidden;
      max-height: 4rem;
      padding-bottom: 1rem;

      &.articles-and-blogs{
        font-family: 'Raleway', sans-serif;
        font-weight: 450;
      }

    }
    .card-content p.card-content-description:after {
      position: absolute;
      z-index: 2;
      bottom: -1px;
      right: 0;
      left: 0;
      height: 1rem;
      content: '';
    }
  }

}

.checkbox {
  cursor: pointer;
  display: flex;
  line-height: 1.25;
  position: relative;
  align-items: center;

  &-count-articles-and-blogs{
    font-size: small;
    font-style: italic;
    color: $enterprise-red;
  }

  &-count-dax{
    font-size: small;
    font-style: italic;
    color: $orange-deep;
  }

}

.checkbox:hover .checkbox-check:before {
  content: '\2714' !important;
}

.checkbox input[type='checkbox'] {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal;
}

.checkbox input[type='checkbox']:checked + .checkbox-check {
  background-color: $link-default;
  border-color: $link-default;
}

.checkbox input[type='checkbox']:checked + .checkbox-check:before {
  content: '\2714' !important;
  color: $white;
}

.checkbox input[type='checkbox'][disabled] + .checkbox-check {
  opacity: 0.6;
  cursor: not-allowed;}


.checkbox .checkbox-check {
  display: flex;
  width: 1.25rem;
  height: 1.25rem;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border: 1px solid $body-color;
  border-radius: 2px;
  transition-property: background, border, border-color;
  transition-duration: 200ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.23, 1);
}

.checkbox .checkbox-check:not(:last-child) {
  margin-right: 0.5rem;
}

.checkbox .checkbox-check:before {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'docons';
  font-size: 0.875rem;
  color: $secondarygrey;
  content: '\0020';
}

.checkbox .checkbox-check.is-checked {
  background-color: $link-default;
  border-color: $link-default;
}

.checkbox .checkbox-check.is-checked:before {
  content: '\2714' !important;
  color: $white;
}

.delete, .modal-close-button, .modal-card-close-button, .pagination-previous,
.pagination-next,
.pagination-link,
.pagination-ellipsis, .tabs, .nav-bar-button, .button, .file {
/* stylelint-disable */
-webkit-touch-callout: none;
-webkit-user-select: none;
-moz-user-select: none;
-ms-user-select: none;
/* stylelint-enable */
user-select: none;
}

.input {
  max-width: 100%;
  width: 100%;

  box-shadow: inset 0 1px 2px $lightshadow-color;
  border-color: $light-border-color;

  &.dax{
    box-shadow: inset 0 1px 2px $lightshadow-color;
    border-color: $border-color;
  }
  &.articles-and-blogs{
    box-shadow: inset 0 1px 2px $shadow-color;
    border-color: $dark-border-color;
  }
  background-color: $white;
  color: $body-color !important;
}

.input:active, input:focus {
  box-shadow: 0 0 0 0.125rem $link-default;
  border-color: $link-default;
  outline-color: $link-default;
  outline-style: solid !important;
  outline-offset: 0 !important;
  outline: none !important;
}

.input:hover {
  border-color:  $link-default;
  z-index: 2;
}

.input.is-small {
  border-radius: 4px;
  font-size: .825rem;
}

.input.is-medium,
.textarea.is-medium {
  font-size: 1rem;
}

.input.is-large,
.textarea.is-large {
  font-size: 1.125rem;
}


.icon {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 1.5rem;
  width: 1.5rem;
}

.icon.is-small {
  height: 1rem;
  width: 1rem;
  font-size: .825rem;
}

.control {
  position: relative;
  font-size: 1rem;
  text-align: left;
}

.control.has-icons-left .input {
  padding-left: 2.25em;
}

.control.has-icons-left .icon, .control.has-icons-right .icon {
  z-index: 4;
  position: absolute;
  top: 0;
  height: 2.25em;
  width: 2.25em;
  color: $border-color;
  pointer-events: none;
}

.control.has-icons-left .icon.is-left {
  left: 0;
}

.iconify.extra-small { width: 16px; height: 16px; }
.iconify.small { width: 24px; height: 24px; }
.iconify.medium { width: 30px; height: 30px; }

.metadata {
  display: block;
  color: $darkgrey;
  font-size: 0.875rem;
}

.home-greeting-container {
  margin-top: 32px;
  margin-bottom: 24px;

  h3 {
    margin: 16px 0;
  }

  @media screen and (max-width: 769px) {
    h1 {
      font-size: 2.3rem;
    }
  }

  @media screen and (min-width: 1024px) {
    p {
      max-width: 768px;
    }
  }
  @media screen and (min-width: 481px) {
    .mobile-show {
      display: none;
    }
  }

  @media screen and (max-width: 480px) {
    .mobile-hidden {
      display: none;
    }
    .mobile-show {
      & h3:hover{
        text-decoration: none !important;
      }
    }
  }

}

.home-nav-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 36px;
  -ms-high-contrast-adjust: none;

  a {
    box-sizing: border-box;
    outline: 1px solid $border-color;
  }

  a img {
    display: block;
    box-sizing: border-box;
    max-height: 80px;
    width: 100%;
    height: 100%;
  }

  a.focus-visible {
    outline-offset: -0.125rem !important;
    z-index: 2;
  }

  @media (min-width: 2559px) {
      max-width: 1600px;
      margin: 24px auto 0;
    }

  @media (max-width: 480px) {
    a {
      width: 100%;
    }
  }

  @media (min-width: 481px) and (max-width: 1083.99999px) {
    a {
      width: 33.3333%;
    }
  }

  @media (min-width: 1084px) {
    a {
      width: 16.6666%;
    }
  }

