/* CONTENT */
.content{

  &.docs{

    h1, h2.likeAnH1{
      overflow-wrap: break-word;
      margin-top: 0;
      margin-bottom: 12px;
      line-height: 1.3;
      font-size: 2.5rem;
      word-break: break-word;
  
      -webkit-animation: fadein 1s forwards, slidein 1.15s forwards;; /* Safari, Chrome and Opera > 12.1 */
      -moz-animation: fadein 1s forwards, slidein 1.15s forwards;; /* Firefox < 16 */
      -ms-animation: fadein 1s forwards, slidein 1.15s forwards;; /* Internet Explorer */
      -o-animation: fadein 1s forwards, slidein 1.15s forwards;; /* Opera < 12.1 */
      animation: fadein 1s forwards, slidein 1.15s forwards;

      @keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }

      @keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }
  
      /* Firefox < 16 */
      @-moz-keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }
      @-moz-keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }
  
      /* Safari, Chrome and Opera > 12.1 */
      @-moz-keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }
      @-moz-keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }
  
      /* Internet Explorer */
      @-ms-keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }
      @-ms-keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }
  
        /* Opera < 12.1 */
      @-o-keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }
      @-o-keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }

    }

  } 

  &.articles-and-blogs{

    h1, h2, h2.likeAnH1, h3, h4, h5, h6 {
      font-size: 100%;
      font-weight: 600;
      font-family: 'Marcellus', serif;
    }

    h1, h2.likeAnH1{
      overflow-wrap: break-word;
      margin-top: 0;
      margin-bottom: 12px;
      line-height: 1.3;
      font-size: 2.5rem;
      word-break: break-word;
    }

    h2{
      margin-top: 32px;
      font-size: 1.75rem;
    }
  
    h3{
      margin-top: 16px;
      font-size: 1.25rem;
    }

    p{
      font-family: 'Fraunces', serif;
      font-size: 1.15rem;
      font-weight: 350;
      line-height: 1.8rem;
    }

    ul, li{
      font-family: 'Fraunces', serif;
      font-size: 1.08rem;
      font-weight: 350;
      line-height: 1.8rem;
    }

    p a:not(.button) {
      text-decoration: none;
      border-bottom: 2px solid $normal-red;
      transition: 120ms linear;
      transition-property: background-color;
      color: $black;
      font-family: 'Source Serif Pro', serif;
      padding-left: 2px;
      padding-right: 2px;
  
      &:visited {
        border-bottom: 2px solid $normal-red;
        color: $black;
      }
  
      &:hover {
        border-bottom: 2px solid $normal-red;
        background-color: $normal-red;
        color: $white;
      }
  
      &:active {
        border-bottom: 2px solid $normal-red;
      }
    }

  }

  .alerts-font{
    font-family: 'Zilla Slab';
    }

  blockquote, dd, dl, dt, fieldset, figure, h1, h2, h3, h4, h5, h6, hr, iframe, legend, li, ol, p, pre, textarea, ul{
    padding: 0;  
    margin: 0;
    }

  h1, h2, h2.likeAnH1, h3, h4, h5, h6 {
    font-size: 100%;
    font-weight: 500;
  }

  h1, h2.likeAnH1{
    overflow-wrap: break-word;
    margin-top: 0;
    margin-bottom: 12px;
    line-height: 1.3;
    font-size: 2.5rem;
    word-break: break-word;
  }


  h2, h3, h4, h5, h6{
    margin-top: 10px;
    margin-bottom: 12px;
    line-height: 1.3;
  }
  
  h1:first-of-type{
    margin-top: -10px;
    margin-bottom: 0;
  }
  
  h2{
    margin-top: 32px;
    font-size: 1.75rem;
    > span.color-spalsh{
      font-family: 'Raleway', sans-serif;
      /* background: -moz-linear-gradient(top,transparent 60%,#ba01ff 50%); */
      /* background: -webkit-linear-gradient(top,transparent 60%,#ba01ff 50%); */
      background: linear-gradient(to bottom,transparent 60%,#ba01ff 50%);
      display: inline;
    }
  }

  h3{
    margin-top: 16px;
    font-size: 1.25rem;
  }

  div p{
    overflow-wrap: break-word;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    margin-left: 0;
    margin-right: 0;
  }

  p{
    overflow-wrap: break-word;
    margin-top: 1rem;
    margin-bottom: 0;
    margin-left: 0;
    margin-right: 0;
  }

  a[href*="//"]:not([href*="github"]):not(.no_icon){
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg%20xmlns=%22http://www.w3.org/2000/svg%22%20viewBox=%220%200%2020%2020%22%3E%3Cstyle%20type=%22text/css%22%3Ecircle,%20ellipse,%20line,%20path,%20polygon,%20polyline,%20rect,%20text%20%7B%20fill:%20rgb(0,%20101,%20179)%20!important;%20%7D%3C/style%3E%3Cpath%20d=%22M17%202c.553%200%201%20.448%201%201v5c0%20.552-.447%201-1%201s-1-.448-1-1V5.414l-7.293%207.293c-.195.195-.45.293-.707.293s-.512-.098-.707-.293c-.39-.39-.39-1.023%200-1.414L14.586%204H12c-.553%200-1-.448-1-1s.447-1%201-1h5zm-4%209c.553%200%201%20.448%201%201v5c0%20.552-.447%201-1%201H3c-.553%200-1-.448-1-1V7c0-.552.447-1%201-1h5c.553%200%201%20.448%201%201s-.447%201-1%201H4v8h8v-4c0-.552.447-1%201-1z%22/%3E%3C/svg%3E%0A");
    background-position: right center;
    background-repeat: no-repeat;
    background-size: 16px 16px;
    padding-right: 18px;
  }

  a h3 {
    color: $link-default;
    &:hover {
      color: $link-hover;
      text-decoration: underline;
    }
  }

  a.title-link h3 {
    color:$black;
    &:hover {
      color:$link-hover-red;
      text-decoration:underline
     }
   }

  p a:not(.button) {
    text-decoration: none;
    border-bottom: 1px solid $link-default;

    &:visited {
      border-bottom: 1px solid $link-visited;
    }

    &:hover {
      border-bottom: 1px solid $link-hover;
    }

    &:active {
      border-bottom: 1px solid $link-active;
    }
  }

  ol.lower-roman li{
    list-style-type:lower-roman;

   }

  ol.numbered-list li{
    margin: 0;
    padding: 0 0 0 1em;
    text-indent: -1em;
    list-style-type: none;
    counter-increment: item;
  }

  ol.numbered-list li:before {
    display: inline-block;
    width: 1em;
    padding-right: 0.5em;
    font-weight: bold;
    text-align: right;
    content: counter(item) ".";
  }

  ol > li, ul > li{
    list-style: disc outside none;
  }
  
  ol, ul{
    margin: 16px 0;
    margin-left: 38px;
  }

  ul.noBullet {
    margin-left: 0;
  }
  
  ul.noBullet > li {
    list-style-type: none;
  }
  
  li {
    outline: 0;
  }

  .rounded-circle{
    transition-property: all;
    transition-duration: 120ms;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
    &:hover{
      font-weight: bold;
      -webkit-box-shadow:0 0px 20px 0 $shadow-color;
      box-shadow:0 0px 20px 0 $shadow-color;
    }
  }

  pre, kbd{
    -moz-osx-font-smoothing: auto;
    -webkit-font-smoothing: auto;
    font-family: SFMono-Regular,Consolas,Liberation Mono,Menlo,Courier,monospace !important;
    line-height: 19px;
  }

  pre{
    overflow-x: auto;
    overflow-y: auto;
    overflow-wrap: normal;
    -webkit-overflow-scrolling: touch;
    padding: 1rem;
    margin: 1rem 0 0;
    word-spacing: normal;
    word-break: normal;
    font-family: monospace,monospace;
    font-size: .825rem;
    white-space: pre;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
    border: 1px solid $border-color;
    background-color: $snowwhite;
  }

  kbd {
    display: inline-block;
    padding: 0.25rem;
    font-family: SFMono-Regular, Consolas, "Liberation Mono", Menlo, Courier, monospace !important;
    font-size: 0.75rem;
    line-height: 10px;
    color: var(--text);
    vertical-align: middle;
    background-color: $snowwhite;
    border: solid 1px $grey;
    border-bottom-color: $grey;
    border-radius: 4px;
    box-shadow: inset 0 -1px 0 $shadow-color;
  }

  code{
    font-family: monospace,monospace;
    font-size: 1em;
    direction: ltr;
    font-family: SFMono-Regular,Consolas,Liberation Mono,Menlo,Courier,monospace;
  }

  :not(a):not(pre) > code{
    overflow-wrap: break-word;
    padding: .1em .2em;
    border-radius: 3px;
    font-size: 85%;
    background-color: $snowwhite;
  }
  
  pre > code{
    display: block;
    position: relative;
    padding: 0;
    border: 0;
    line-height: 19px;
  }

  .copy-code-container {
    box-sizing: content-box;
    font-size: .8rem;
    color: $body-color;
    display: flex;
    justify-content: space-between;
    background-color: $greywhite;
    margin: 1rem 0 -1rem;
    border: 1px solid $border-color;
    border-bottom: 0;
    min-height: 30px;
  }

  .copy-code-container > .language-name {
    padding: 2px 16px;
    flex-grow: 1;
  }

  .copy-code-button {
    padding: 2px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: normal;
    border: 0 solid $border-color;
    border-left-width: 1px;
    cursor: pointer;
    background-color: transparent;
    color: $body-color;

    &::before {
        content: "Copy";
    }

    &::after {
        margin-left: 4px;
        font-family: "Font Awesome 5 Free"; font-weight: 400; content: "\f0c5";
        width: 1em;
        font-size: 1.1em;
    }

    // This class will be toggled via JavaScript
    &.copied {
      background-color: $grassgreen;
      color: $white;
        &::before {
            content: "Copied!";
        }

        &::after {
            color: $white;
            content: "\2714";
            font-size: 1.1em;
        }
    }
  }

  .stepper {
    .line {
      width: 2px;
      background-color: $lightgrey !important;
    }
    .lead {
      font-size: 1.1rem;
    }
  }
  
  table {
    margin-top: 1.0rem;
    table-layout: auto;
    width: 100%;
    font-size: 0.825rem;

    caption {
      font-size: 0.8rem;
      font-weight: 600;
      letter-spacing: 2px;
      text-transform: uppercase;
      padding: 1.125rem;
      border-width: 0 0 1px;
      border-style: solid;
      border-color: $border-color;
      text-align: right;
    }

    td, th {
      display: table-cell;
      word-wrap: break-word;
      padding: 0.75rem 1rem;
      line-height: 1.5;
      vertical-align: top;
      border-top: 1px solid $border-color;
      border-right: 0;
      border-left: 0;
      border-bottom: 0;
      border-style: solid;
    }

    td p:first-child,
    th p:first-child {
      margin-top: 0;
    }

    td.nowrap,
    th.nowrap {
      white-space: nowrap;
    }

    td.is-narrow,
    th.is-narrow {
      width: 15%;
    }

    th:not([scope='row']) {
      border-top: 0;
      border-bottom: 1px;
    }

  }

  figure{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
    figcaption {
      text-align: center;
    }
  }

  .docimage{
    height: auto;
    margin-top: 0.5rem;
    padding: 2.8%;
    transition-property: all;
    transition-duration: 120ms;
    transition-timing-function: linear;
    transition-delay: 0s;

    &:hover{
      border-radius: 4px;
      -webkit-box-shadow:0 0px 24px 0 $shadow-color;
      box-shadow:0 0px 24px 0 $shadow-color;
    }

    &.no-hover{
      border-radius: 0px;
      -webkit-box-shadow:0 0px 0px 0 $shadow-color;
      box-shadow:0 0px 0px 0 $shadow-color;
    }
  }

  .img-wrap {
    position: relative;
  }

  .img-wrap:hover .img-hover-description-layer {
    visibility: visible;
    opacity: 1;
  }

  .img-hover-description-layer{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    visibility: hidden;
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .img-wrap:hover .img-hover-description {
    transform: translateY(0);
    color: $red;
    font-size: 0.75rem;
    font-weight: 500;
    font-family:'Roboto',sans-serif;
  }

  /* MODAL */
   @import "blocks/main/content_modal";



  /* Video Wrapper */
  .videoWrapper {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
    height: 0;
    display: flex; 
    justify-content: center;
  }
  .videoWrapper:hover {
   // border: solid 0.05rem;
   // border-style: inset;
   // border-color: #d7d9e0;
  }
  .videoWrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 1.0rem 1.0rem;
  }

  /* Header Colours */
  h2.colored-orange {
    padding-left: 1.2rem;
    padding-right: 1.0rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background: linear-gradient(45deg, #fe0061, #ffeb3b);
    transform: translateZ(5.0rem);
    color: $white;
    border-radius: 3.0rem 1.2rem;
  }

  h2.colored-green {
    padding-left: 1.2rem;
    padding-right: 1.0rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background: linear-gradient(45deg, #04bdca, #ffd51a);
    transform: translateZ(5.0rem);
    color: $white;
    border-radius: 3.0rem 1.2rem;
    //background: linear-gradient(45deg, #0024c2, #a0e4ff);
  }

  h2.colored-magenta {
    padding-left: 1.2rem;
    padding-right: 1.0rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background: linear-gradient(45deg, #990045, #ff9c62);
    transform: translateZ(5.0rem);
    color: $white;
    border-radius: 3.0rem 1.2rem;
    //background: linear-gradient(45deg, #0024c2, #a0e4ff);
  }

  h2.color-splash-red {
    font-family: 'Raleway', sans-serif;
    font-weight: 900;
    padding-bottom: 0.15rem;
    background: linear-gradient(to bottom,transparent 60%,#ff1201 50%);
    display: inline;
  }

  h3.color-splash-orange {
    font-family: 'Raleway', sans-serif;
    font-weight: 900;
    padding-bottom: 0.15rem;
    background: linear-gradient(to bottom,transparent 60%,#ff5e01 50%);
    display: inline;
  }

}





























