.nav-bar {

  display: flex;
  overflow: hidden;
  justify-content: space-between;
  align-items: stretch;
  min-height: 44px;
  height: 44px;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  border-bottom: 1px solid $lightgrey;

  @media screen and (min-width: 768px), print {
      padding-left: 1rem;
      padding-right: 1rem;
  }

  @media screen and (min-width: 860px), print {
      padding-left: 24px;
      padding-right: 24px;
  }

  @media screen and (min-width: 1084px), print {
      padding-left: 5vw;
      padding-right: 5vw;
  }

  @media screen and (min-width: 1795px), print {
      padding-left: calc(50% - 1600px / 2);
      padding-right: calc(50% - 1600px / 2);
  }

  .nav-bar-logo {
    position: relative;
     width: 144px;
     height: 40px;
     background-size: contain;

    .overlay {
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0;
      transition: .4s ease;
      background-color: $white;
    }

    &:hover .overlay {
      opacity: 1;
    }

    .alt-logo {
      color: $red;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      text-align: center;
    }
  }

  .nav-bar-button {
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: stretch;
    flex-shrink: 0;
    flex-grow: 0;
    min-width: 54px;
    padding: 0 0.5rem;
    background-color: transparent;
    color: $body-color;
    border: none;
    text-decoration: none !important;
    white-space: nowrap;
    font-size: 0.800rem;
    line-height: normal;
    cursor: pointer;
    outline-offset: -0.125rem !important;
    transition-duration: 120ms;
    transition-timing-function: linear;
    transition-delay: 0s;
    transition: all .12s linear;
    

    &.underline,  &.underline:visited {
      color: $deep-red;
    }

    &.underline > span{
      position: relative;
    }

    &.underline > :first-child:after{
      content: '';
      display: block;
      position: absolute;
      border-bottom: 3px solid;
      left: 0;
      right: 0;
      bottom: -0.325rem;
    }

    &:visited {
      color: $body-color;
    }

    &.is-title, &.nav-bar-menu {
      font-size: 1rem;
      word-break: break-word;
      padding: 0;
      background-color: transparent;
      line-height: normal;
  
      @media screen and (min-width: 768px), print {
          font-size: 1.125rem;
          font-weight: 600;
          line-height: 1.125;
          margin: 0;
          padding: 0 0.5rem;
      }
    }
  }

  & > .nav-bar-button:first-child {
    padding-left: 0;
  }

  .nav-bar-button:active.has-hover-underline:not([aria-expanded='true']) > :first-child:after, .nav-bar-button.is-active.has-hover-underline:not([aria-expanded='true']) > :first-child:after, .nav-bar-button:hover.has-hover-underline:not([aria-expanded='true']) > :first-child:after, .nav-bar-button.focus-visible.has-hover-underline:not([aria-expanded='true']) > :first-child:after {
    content: '';
    display: block;
    position: absolute;
    border-bottom: 3px solid;
    left: 0;
    right: 0;
    bottom: -0.325rem;
  }

  .nav-bar-button:active.has-hover-underline:not([aria-expanded='true']) > :first-child, .nav-bar-button.is-active.has-hover-underline:not([aria-expanded='true']) > :first-child, .nav-bar-button:hover.has-hover-underline:not([aria-expanded='true']) > :first-child, .nav-bar-button.focus-visible.has-hover-underline:not([aria-expanded='true']) > :first-child {
    position: relative;
  }

  .nav-bar-button:active, .nav-bar-button.is-active, .nav-bar-button:hover, .nav-bar-button.focus-visible {
    color: $deep-red;
  }

  .nav-bar-button.dropdown-trigger[aria-expanded='true'] {
    background-color: $snowwhite;
  }

  .nav-bar-button.is-title > .nav-bar-menu-title, .nav-bar-button.nav-bar-menu > .nav-bar-menu-title {
    padding-left: 0.5rem;
    padding-bottom: 0.25rem;
    font-weight: 600;
  }

  .delete, .modal-close-button, .modal-card-close-button, .pagination-previous,
  .pagination-next,
  .pagination-link,
  .pagination-ellipsis, .tabs, .nav-bar-button, .button, .file {
    /* stylelint-disable */
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    /* stylelint-enable */
    user-select: none;
  }

  .nav-bar-nav {
    display: none;
    align-items: stretch;

    @media screen and (min-width: 768px), print {
        display: flex;
    }

    .nav-bar-nav-list {
      display: flex;
      align-items: stretch;

      .nav-bar-item {
        display: flex;
        align-items: center;
        flex-shrink: 0;

        &.has-spacing {
          margin: 0 0.5rem;
        }

        &.is-category:before {
          content: '';
          display: inline-block;
          height: 1.5rem;
          border-left: 2px solid $darkgrey;
          margin-left: 0.5rem;
          margin-right: 1rem;
        }
      }
    }
  }

  .nav-bar-spacer {
    display: none;
    flex-grow: 1;
    flex-shrink: 1;

    @media screen and (min-width: 768px), print {
        display: block;
    }
  }

}


 
      


 

 




   