/* Search Filter */
.filter-icon{
  display: inline-block;
  height:0.9rem;
  width: 1.0rem;
  text-transform: none;
  text-align: center;
}

.searchFilter {
  display: inline-block;
  position: relative;

  .searchFilter-input {
    padding-right: 26px;
  }

  .searchFilter-suggestions {
    list-style-type: none;
    z-index: 1;
    position: absolute;
    max-height: 18rem;
    min-width: 100%;
    max-width: 100%;
    padding: 0;
    margin: 2px 0 0 !important;
    cursor: default;
    box-shadow: 0 1.6px 3.6px 0 $lightshadow-color,0 .3px .9px 0 $lightshadow-color;
    border: 1px solid $border-color;
    background-color: $white;

    @media screen and (min-width: 768px), print {
        max-width: 500px;
    }

    .searchFilter-suggestion {
      display: block;
      border: 1px solid transparent;
      a {
        color: $body-color;
        text-decoration: none;
      } 
      &:hover{
        background-color: $greywhite;
        border: 1px solid $white;
      }
    }
  }
}