/* HEADER */

.header-holder {
    flex-shrink: 0;
    background-color: $white;
    z-index: 999;

    &.transparent{
      background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(255,255,255,0.95) 55%, rgba(255,255,255,0.75) 100%);
    }

    /* The progress container */
    .progress-container {
      width: 100%;
      height: 2px;
      background: transparent;
    }
 
    /* The progress bar (scroll indicator) */
    .progress-bar-scroll {
      height: 2px;
      background: $normal-red;
      width: 0%;
    }

  .content-header {
    margin: 0;
    border-bottom: 1px solid $light-border-color;
  
    @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
        padding: 0;
    }
  
    @media screen and (min-width: 768px), print {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0;
    }
  
    .breadcrumbs {
      width: 100%;
      background: $snowwhite;
      font-size: 0.825rem;
      margin-bottom: 0;
      padding: 0 1rem;
  
      @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
          border-bottom: 1px solid $border-color;
        }
  
      @media screen and (min-width: 768px), print {
          padding: 0;
          background: none;
          width: auto;
        & > li {
          display: inline-block;
        }
      }
  
      & > li {
        display: inline;
        text-decoration: line;
  
        a span{
          padding: 0.45rem 0.30rem 0.45rem 0.30rem;
          width: 100%;
          font-size: 0.825rem;
          transition: 0.05s;
        }
  
        &.is-active a{
          //margin-bottom: 0.5rem;
          //display: inline;
          //font-weight: bold;
          color: $deep-red;
          //border-bottom: 2px solid #e82127;
          left: 0;
          right: 0;
        }
  
        span:hover{
          display: inline;
          bottom: -0.125rem;
          border-radius: 2px;
          color:$white;
          background-color:$red;
          -webkit-box-shadow:0 7px 14px 0 $shadow-color;
          box-shadow:0 7px 14px 0 $shadow-color;
          -webkit-transform:translateY(-2px);
          transform:translateY(-2px)
        }
    
        &:not(:last-of-type):after {
          content: '/';
        }
    
        &:after {
          display: inline-block;
          margin: 0px 6px;
          color: $darkish-grey;
        }
      }
    }
  
    .button{
  
      & {
        overflow: visible;
        border-radius: 2px;
        border-color: $body-color;
        border-width: 1px;
        background-color: $white;
        color: $body-color;
        cursor: pointer;
        justify-content: center;
        //min-width: 120px;
        padding-bottom: calc(0.375em - 1px);
        padding-left: 0.75em;
        padding-right: 0.75em;
        padding-top: calc(0.375em - 1px);
        text-align: center;
        white-space: nowrap;
      }
  
      &.is-fullwidth {
        display: flex;
        width: 100%;
      }
  
      &.is-small {
        border-radius: 2px;
        font-size: 0.825rem;
      }
  
      .icon {
        &, &.is-small, &.is-medium, &.is-large
        {
          width: 1.5em;
        }
        &:first-child:last-child {
          margin-left: calc(-0.375em - 1px);
          margin-right: calc(-0.375em - 1px);
        }
        &:first-child:not(:last-child) {
          //margin-left: calc(-0.375em - 1px);
          margin-right: 0.1875em;
          }
        &:last-child:not(:first-child) {
          // margin-left: 0.1875em;
          margin-right: calc(-0.375em - 1px);
          }
      }
  
      &:hover, &.is-hovered {
        color: $body-color;
        background-color: $snowwhite;
      }
  
      &:not(.is-text), &:not(.is-text-primary), &:not(.is-link) {
        text-decoration: none !important;
      }
  
      &.is-text {
        background-color: transparent;
        border-color: transparent;
        border-width: 0px;
        color: $body-color;
        transition: 0.05s;
  
        i.colorfill{
          color: $red;
        }
  
        &:hover {
          -webkit-box-shadow:0 2px 7px 0 $shadow-color;
          box-shadow:0 2px 7px 0 $shadow-color;
          -webkit-transform:translateY(-1px);
          transform:translateY(-1px);
          border-radius: 2px;
          } 
  
        &.colorfill{
          &:hover, &.is-hovered, &.focus-visible, &.is-focused {
            color:$white;
            background-color:$red;
            -webkit-box-shadow:0 7px 14px 0 $shadow-color;
            box-shadow:0 7px 14px 0 $shadow-color;
          }
        }
  
        &:hover > span.icon.effect {
          color:$white;
          background-color:$red;
          -webkit-box-shadow:0 7px 14px 0 $shadow-color;
          box-shadow:0 7px 14px 0 $shadow-color;
          -webkit-transform:translateX(-2px);
          transform:translateX(-2px);
          border-radius: 2px;
  
          i.colorfill{
            color:$white;
          }
        }
        
        &:visited {
          color: $body-color;
        }
  
        &:active, &.is-active {
          background-color: $greywhite;
          color: $body-color;
        }
  
        &.is-disabled, &[disabled] {
          background-color: transparent;
          border-color: transparent;
          box-shadow: none;
        }
  
        &.has-inner-focus {
          outline-color: $body-color;
        }
  
        &.has-text-primary {
          color: $link-default;
  
          &:hover, &.is-hovered, &.focus-visible, &.is-focused {
            color:$link-default;
            background-color:$snowwhite;
            -webkit-box-shadow:0 7px 14px 0 $shadow-color;
            box-shadow:0 7px 14px 0 $shadow-color;
            -webkit-transform:translateY(-2px);
            transform:translateY(-2px)
          }
  
          &:visited {
            color: $link-default;
          }
  
          &:active, &.is-active {
            background-color: $greywhite;
            color: $greywhite;
          }
        }
  
      }
    }
  
    .dropdown {
      position: relative;
    }
  
    .sharing-menu {
      position: absolute;
      margin-top: 12px;
      z-index: 100;
      width: auto;
      //left: 0px;
      top: 100%;
      white-space: nowrap;
      background-color: $white;
      border: 1px solid $border-color;
      box-shadow: 0 6.4px 14.4px 0 $lightshadow-color, 0 1.2px 3.6px 0 $lightshadow-color;
  
      @media screen and (max-width: 1087px), screen and (min-resolution: 120dpi) and (max-width: 1087.9px) {
          right: 0px;
      }
  
      .is-hidden {
        display: none;
      }
    }
  
  }
  
}

