/* WEBSITE LINKS */

a {
    color: $link-default;
    outline: 0;
    cursor: pointer;
    text-decoration: none;
    word-wrap: break-word;

    &:hover{
    color: $link-hover;
    text-decoration: none;
    }

    &:visited{
        color: $link-visited;
    }

    &:active {
        color: $link-active;
    }

    &.view-on-github:hover{
        text-decoration: underline;
    }
}
