%page{
    display: flex;
    align-items: center;
    margin-top: 1.2rem;
    padding: 0.70rem;
    background-color: $sharp-red;
    color: $white;
    transition-property: all;
    transition-duration: 120ms;
    transition-timing-function: linear;
    transition-delay: 0s;
}

%pageHover{
    background-color: $white; 
    color: $deep-red;
    border: 1px solid $deep-red;
    -webkit-box-shadow:0 4px 14px 0 $shadow-color;
    box-shadow:0 4px 14px 0 $shadow-color;
    border-radius: 2px; 
}

%isFaded{
    background-color: $tingegrey;
    color: $grey;
}

%isFadedHover{
    background-color: $tingegrey;
    color: $grey;
    border: 1px solid $darkish-grey;
    -webkit-box-shadow:0 4px 14px 0 $shadow-color;
    box-shadow:0 4px 14px 0 $shadow-color;
}

#nextPage{
    @extend %page;
    margin-right: 0.2rem;
    &:hover{
        @extend %pageHover; 
    }
    &.is-faded{
    @extend %isFaded; 
        &:hover{
            @extend %isFadedHover; 
        }
    }
}

#previousPage{
    @extend %page;
    margin-left: 0.2rem;
    &:hover{
        @extend %pageHover; 
    }
    &.is-faded{
    @extend %isFaded; 
        &:hover{
            @extend %isFadedHover; 
        }
    }

}
