.doc-outline {
  h3 {
    margin: 0;
    margin-top: 16px;
    font-size: 1rem;
    @media screen and (min-width: 768px), print {
        margin-top: 0;
    }
  }
  ul {
    margin: 10px 0 0;
    list-style-type: none;
    li {
      list-style: none;
      a {
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  li {
    border-left: 3px solid transparent;
    padding: 2px 0;
    padding-left: 4px;
    margin: 4px 0;
    line-height: 1.3;
    font-size: 0.825rem;
    &.selected {
      font-weight: 600;
      border-color: $link-default;
    }
  }
  a:visited {
    color: $link-default;
  }

}
  

#side-doc-title {
  margin-top: 0.25rem;
  margin-right: 0.15rem;
  transition: 0.15s;
  &:hover {
    color:$black;
    background-color:$snowwhite;
    -webkit-box-shadow:2px 2px 2px 2px $shadow-color;
    box-shadow:2px 2px 2px 2px $shadow-color;
    -webkit-transform:translateY(-2px);
    transform:translateY(-2px);
    border-radius: 3px;
    border: 0.8px solid $deep-red;
    padding: 0.15rem;
    }
}


.top-link {
  border-color: $grey;
  border-width: 2px;
  border-radius: 50%;
  position: fixed;
  bottom: 0;
  right: 7%;
  align-items: center;
  justify-content: center;
  display: inline-flex;
  cursor: pointer;
  margin: 0 1.5em 1.5em 0;
  padding: .25em;
  width: 35px;
  height: 35px;
  background-color: $snowwhite;
  transition: all .25s ease-in-out;

  -webkit-box-shadow:0.75px 0.9px 2px 2px $shadow-color;
  box-shadow:0.75px 0.9px 2px 2px $shadow-color;
  -webkit-transform:translateY(-2px);
  transform:translateY(-2px);
  
  &.show {
    visibility: visible;
    opacity: 1;
  }
  
  &.hide {
    visibility: hidden;
    opacity: 0;
  }

  svg {
      fill: $red;
      width: 16px;
      height: 12px;
  }

  &:hover {
      background-color: $red;

      svg {
          fill: $white;
      }
  }
}