/* MAIN CONTAINER */
.mainContainer {
  flex-grow: 1;
  flex-shrink: 1;

  /* LEFT SIDEBAR */
  .left-sidebar {
    padding-top: 1.5rem;

    /* LHS SEARCH FILTER */
    @import "blocks/main/searchfilter";

    /* LHS TOC */
    @import "blocks/main/lhstoc";

    #affixed-left-sidebar {
      z-index: 1;
    }
  }

  /* PRIMARY HOLDER */
  .primary-holder{
    padding-top: 1.5rem;

    /* MAIN CONTENT */
    #main-column {
    position: relative;

      /* CONTENT */
      @import "blocks/main/content";

      /* MAIN NAV BOTTOM */
      #mainNavBottom {
        display: flex;
        justify-content: space-between;
        margin-top: 1.0rem;

        @import "blocks/main/nav_bottom";
      }
    }
  }

  /* RIGHT SIDEBAR */
  .right-sidebar{
    padding-top: 1.5rem;

    /* RHS TOC */
    @import "blocks/main/rhstoc";

    #affixed-right-sidebar {
      z-index: 1;
    }
  }

}


/* UHF CONTAINER */
.uhf-container {

  margin-right: auto;
  margin-left: auto;
  max-width: 100%;

  /* CONTAINER */

  &.container {
    padding: 0;
    margin: 0 auto;
    position: relative;
    @media screen and (min-width: 1088px) {
        max-width: 960px;
        width: 960px;
      &.is-fluid {
        margin-left: 64px;
        margin-right: 64px;
        max-width: none;
        width: auto;
      }
    }
    @media screen and (max-width: 1279px), screen and (min-resolution: 120dpi) and (max-width: 1279.9px) {
      &.is-widescreen {
        max-width: 1152px;
        width: auto;
      }
    }
    @media screen and (max-width: 1471px), screen and (min-resolution: 120dpi) and (max-width: 1471.9px) {
      &.is-fullhd {
        max-width: 1344px;
        width: auto;
      }
    }
    @media screen and (min-width: 1280px) {
        max-width: 1152px;
        width: 1152px;
    }
    @media screen and (min-width: 1472px) {
        max-width: 1344px;
        width: 1344px;
    }
  }


  &.has-padding {
    width: 100%;
    margin-right: 0;
    margin-left: 0;
  }

  &.has-top-padding {
    padding-top: 1.25rem;
  }

  &:not(.has-padding) {
    @media screen and (max-width: 859px) {
        max-width: calc(100% - 32px);
        width: calc(100% - 32px);
    }
    @media screen and (min-width: 860px), print {
      max-width: calc(100% - 48px);
      width: calc(100% - 48px);
    }
    @media screen and (min-width: 1084px), print {
        max-width: calc(100% - 10vw);
        width: calc(100% - 10vw);
      &.is-fluid {
        margin-right: 5vw;
        margin-left: 5vw;
        max-width: none;
        width: auto;
        }
    }
    @media screen and (min-width: 1795px), print {
      margin-right: auto;
      margin-left: auto;
      max-width: 1600px;
      width: 1600px;
    }
  }

  &.has-padding {
    @media screen and (max-width: 859px), print {
        padding-right: 0;
        padding-left: 0;
      & > * {
        padding-right: 16px;
        padding-left: 16px;
      }
    }
    @media screen and (min-width: 860px), print {
        padding-right: 24px;
        padding-left: 24px;
    }
    @media screen and (min-width: 1084px), print {
        padding-right: 5vw;
        padding-left: 5vw;
    }
    @media screen and (min-width: 1795px), print {
        padding-right: calc(50% - 1600px / 2);
        padding-left: calc(50% - 1600px / 2);
    }
  }

  &.is-full {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    max-width: 100%;
  }

}


/* COLUMNS */
.columns {
  margin-left: -0.75rem;
  margin-right: -0.75rem;
  margin-top: -0.75rem;

    &:last-child{
      margin-bottom: -0.75rem;
    }

    &:not(:last-child){
      margin-bottom: calc(1.5rem - 0.75rem);
    }

    &.has-large-gaps{
      margin-left: -1.5rem;
      margin-right: -1.5rem;
      margin-top: -1.0rem;
    }

    &.has-large-gaps:last-child {
      margin-bottom: -1.5rem;
    }

    &.has-large-gaps:not(:last-child) {
      margin-bottom: calc(1.5rem - 1.5rem);
    }

    &.is-gapless {
      margin-left: 0;
      margin-right: 0;
      margin-top: 0;
    }

    &.is-gapless:not(:last-child) {
      margin-bottom: 1.5rem;
    }

    &.is-gapless:last-child {
      margin-bottom: 0;
    }

    &.is-gapless > .column:not(.has-uhf-padding) {
      margin: 0;
      padding: 0 !important;
    }

    &.is-multiline {
      flex-wrap: wrap;
    }

    &.is-mobile {
      display: flex;
    }

    @media screen and (min-width: 768px), print {
      &:not(.is-desktop) {
        display: flex;
      }
    }

    @media screen and (min-width: 1088px) {
      .column.is-desktop {
        display: flex;
      }
    }

    @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
      &.is-gapless-mobile {
        margin-left: 0;
        margin-right: 0;
        margin-top: 0;

        & > .column:not(.has-uhf-padding) {
          margin: 0;
          padding: 0 !important;
        }
        &:not(:last-child) {
          margin-bottom: 1.5rem;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    & > .column {
      display: block;
      flex-basis: 0;
      flex-grow: 1;
      flex-shrink: 1;

      &:not(.has-uhf-padding) {
        padding: 0.75rem;
      }

      @media screen and (min-width: 768px), print {

        &.is-one-third-tablet {
          flex: none;
          width: 33.3333%;
        }
        &.is-one-quarter-tablet {
          flex: none;
          width: 25%;
        }
        &.is-one-fifth-tablet {
          flex: none;
          width: 20%;
        }
        &.is-two-thirds-tablet {
          flex: none;
          width: 66.6666%;
        }
        &.is-three-quarters-tablet {
          flex: none;
          width: 75%;
        }
        &.is-four-fifths-tablet {
          flex: none;
          width: 80%;
        }
        &.is-full {
          flex: none;
          width: 100%;
        }
        &.is-one-quarter {
          flex: none;
          width: 25%;
        }
        &.is-9, &.is-9-tablet {
          flex: none;
          width: 75%;
        }
        &.is-6, &.is-6-tablet {
          flex: none;
          width: 50%;
        }
        &.is-4, &.is-4-tablet {
          flex: none;
          width: 33.33333%;
        }
        &.is-two-fifths, &.is-two-fifths-tablet {
          flex: none;
          width: 40%;
        }
        &.is-three-fifths, &.is-three-fifths-tablet {
          flex: none;
          width: 60%;
        }
      }

      @media screen and (min-width: 1088px) {

        &.is-one-quarter-desktop {
          flex: none;
          width: 25%;
        }
        &.is-three-quarters-desktop {
          flex: none;
          width: 75%;
        }
        &.is-four-fifths-desktop {
          flex: none;
          width: 80%;
        }
        &.is-five-sixths-desktop {
          flex: none;
          width: 83.33333%;
        }
        &.is-six-sevenths-desktop {
          flex: none;
          width: 85.71%;
        }
        &.is-one-fifth-desktop {
          flex: none;
          width: 20%;
        }
        &.is-one-sixth-desktop {
          flex: none;
          width: 16.66666%;
        }
        &.is-one-seventh-desktop {
          flex: none;
          width: 14.29%;
        }
        &.is-10-desktop {
          flex: none;
          width: 83.33333%;
        }
        &.is-4-desktop {
          flex: none;
          width: 33.33333%;
        }
        &.is-3-desktop {
          flex: none;
          width: 25%;
        }
      }

      @media screen and (min-width: 1088px) {

        &.is-one-sixth-widescreen {
          flex: none;
          width: 16%;
        }

        &.is-one-fifth-widescreen {
          flex: none;
          width: 20%;
        }

        &.is-one-quarter-widescreen {
          flex: none;
          width: 25%;
        }
  
        &.is-three-quarters-widescreen {
          flex: none;
          width: 75%;
        }

        &.is-five-sixths-widescreen {
          flex: none;
          width: 84%;
        }

      }

    }


    &.has-large-gaps > .column {
      padding: 1.5rem;
      padding-top: 1.0rem;
    }

    &.is-mobile > .column.is-narrow {
      flex: none;
    }
}